export default {
	data() {
		return {
			message:'发送验证码',
			isSend:false,
			phone:'',
			checked:true,
			code:'',
			checkPwd:false
		};
	},
	onLoad(options){
		let type=options.type
		if(type){
			this.checkPwd = true
		}
	},
	methods:{
		changeInput(e,name){
			this[name] = e.detail.value
			console.log(this.phone)
		},
		sendMark(){
			if(this.isSend){
				return false;
			}
			if(this.phone == ''){
				this.$acFrame.Util.mytotal('请输入手机号码')
				return false
			} else {
				let reg = /^1[345789]\d{9}$/
				if(!reg.test(this.phone)){
					this.$acFrame.Util.mytotal('请输入正确的手机号码')
					return false
				}
			}
			let num =10;
			let self = this
			let timer=null
			this.getMark();
			function downCount(){
				if(num>0){
					num--
					self.isSend = true
					self.message= num+'s后重新发送'
					
				}else{
					self.isSend=false
					clearInterval(timer)
					self.message= '发送验证码'
				}
			}
			setInterval(()=>{
				timer=downCount()
			},1000)
		},
		getMark(){
			let self = this
			let params={
				mobilePhone:this.phone,
				type:1
			}
			this.$acFrame.HttpService.sendMark(params).then(res=>{
				if(res.success){
					this.$acFrame.Util.mytotal('发送验证码成功！')
				}
			})
		},
		bindPhone(){
			let self = this
			let params={
				checkCode:this.code,
				mobilePhone:this.phone,
				type:1
			}
			if(this.phone == ''){
				this.$acFrame.Util.mytotal('请输入手机号码')
				return false
			} else {
				let reg = /^1[345789]\d{9}$/
				if(!reg.test(this.phone)){
					this.$acFrame.Util.mytotal('请输入正确的手机号码')
					return false
				}
			}
			if(this.code == ''){
				this.$acFrame.Util.mytotal('请输验证码')
				return false
			}
			this.$acFrame.HttpService.submitMark(params).then(res=>{
				if(res.success){
					this.$acFrame.Util.mytotal('绑定成功！')
					setTimeout(()=>{
						uni.navigateBack({})
					},1000)
				}
			})
		}
	}
}
